import React from 'react';
import './BottomMenu.css'; // Подключение файла стилей
import Friends from "../../assets/images/Friends.svg"
import Upgrade from "../../assets/images/Upgrade.svg"
import Mine from "../../assets/images/Mine.svg"
import Tasks from "../../assets/images/Tasks.svg"
import Stream from "../../assets/images/Stream.svg"
import { Link } from 'react-router-dom';


const BottomMenu = () => {
  return (
    <nav className="menu-bottom">

    <div className='menu-button'>
    <Link className="menu-button-items" to="/friends">
        <img src={Friends} alt="Friends Icon"/>
        <span>Friends</span>
      </Link>
    </div>

    <div className='menu-button'>
      <a className="menu-button-items" href="https://ya.ru/">
        <img src={Upgrade} alt="Upgrade Icon" />
        <span>Upgrade</span>
      </a>
    </div>

    <div className='menu-button'>
    <Link className="menu-button-items" to="/">
        <img src={Mine} alt="Mine Icon" />
        <span>Mine</span>
      </Link>
    </div>

    <div className='menu-button'>
      <a className="menu-button-items" href="https://ya.ru/">
        <img src={Tasks} alt="Mine Icon" />
        <span>Tasks</span>
      </a>
    </div>

    <div className='menu-button'>
    <Link className="menu-button-items" to="/settings">
        <img src={Stream} alt="Mine Icon" />
        <span>Stream</span>
      </Link>
    </div>

    </nav>
  );
};

export default BottomMenu;
