// src/ConnectWallet.js
import React, { useEffect, useState } from 'react';
import { TonConnectButton ,useTonConnectUI } from '@tonconnect/ui-react';
import { TonConnect } from '@tonconnect/sdk';

const tonConnect = new TonConnect({
  // Укажите параметры вашего проекта
  manifestUrl: 'http://localhost:3000/whale_bot/tonconnect-manifest.json', // URL вашего манифеста
});

const TonWallet = () => {
  const [address, setAddress] = useState(null);
  const { wallet, disconnect } = useTonConnectUI();

  // Listen for wallet connection
  const handleConnect = () => {
    if (wallet) {
      setAddress(wallet.account.address);
    }
  };

  // // Disconnect function (optional)
  // const handleDisconnect = () => {
  //   disconnect();
  //   setAddress(null);
  // };

  return (
    <div>
      <h2>Подключите свой кошелек TON</h2>
      <TonConnectButton onConnect={handleConnect} />
      {address && (
        <div>
          <p>Подключенный адрес: {address}</p>
          <button>Отключить кошелек</button>
        </div>
      )}
    </div>
  );
};

export default TonWallet;
