import React, { useState, useEffect }  from 'react';
import './Friends.css';
import Headers  from '../Headers/Headers';
import BottomMenu from '../BottomMenu/BottomMenu'
import Drop from "../../assets/images/DropFriend.svg"

const Friends = () => {
    const [activeTab, setActiveTab] = useState('friends');

    const handleTabChange = (tab) => {
      setActiveTab(tab);
    };

    return (

        <div className='background-friends'>
            <Headers></Headers>


        <div className="tabs-menu">
            <button
                className={`tab-button ${activeTab === 'friends' ? 'active' : ''}`}
                onClick={() => handleTabChange('friends')}
            >
                Friends
            </button>

            <button
                className={`tab-button ${activeTab === 'leaderboards' ? 'active' : ''}`}
                onClick={() => handleTabChange('leaderboards')}
            >
                Leaderboards
            </button>
        </div>


        <div className="tab-content">
            {activeTab === 'friends' && <FriendsContent />}
            {activeTab === 'leaderboards' && <LeaderboardsContent />}
        </div>

        <BottomMenu></BottomMenu>
        
            
        </div>
    );
};


const FriendsContent = () => {
    const [referralLink] = useState('test'); // Состояние для реферальной ссылки
    const referrals = [
        { name: 'John Doe', coin: 9999},
        { name: 'Jane Smith', coin: 15},
        { name: 'Mike Johnson', coin: 15 },
        { name: 'Anna Williams', coin: 15 },
        { name: 'Paul Allen', coin: 15 },
        { name: 'Lucy White', coin: 15 },
        { name: 'Tom Brown', coin: 15 },
        { name: 'Sara Green', coin: 15 },
        { name: 'Chris Black', coin: 15 },
        { name: 'Diana Silver', coin: 15 },
      ];
      
    const copyToClipboard = () => {
      navigator.clipboard.writeText(referralLink).then(() => {
        alert('Ссылка скопирована в буфер обмена!'); // Уведомление о копировании
      }).catch((err) => {
        console.error('Ошибка при копировании: ', err);
      });
    };
  
    return (
      <div className='friend-content'>
        <div className='tab-headline'>Friends</div>
            <div className='referral-container'>
                <input
                    type="text"
                    value={referralLink}
                    readOnly
                    className="referral-input"
                    onClick={copyToClipboard} // Копируем при нажатии на поле
                />

                <button className="send-button" onClick={() => alert('Ссылка отправлена!')}>
                    Send
                </button>
                
            </div>


            <div className='referral-alert'>
            Каждый приглашенный тобою друг принесет дополнительных 25 монет, поспеши!
            </div>


            <div className='referral-list'>
                <div className='ref-text'>
                    Список тех, кто с тобой в команде
                </div>

                <div className='ref-friend'>
                {referrals.map((referral, index) => (
                    <div className='friend-item' key={index}>
                      <span className='friend-info'>
                        {index + 1}. {referral.name}
                      </span>
                      <div className='friend-right'>
                        <img src={Drop} alt="Drop Image" />
                        <span className='friend-coin'>
                          {referral.coin}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
            </div>

      </div>
    );
  };
  

  const LeaderboardsContent = () => {

    const referrals = [
      { name: 'John Doe', coin: 9999},
      { name: 'Jane Smith', coin: 15},
      { name: 'Mike Johnson', coin: 15 },
      { name: 'Anna Williams', coin: 15 },
      { name: 'Paul Allen', coin: 15 },
      { name: 'Lucy White', coin: 15 },
      { name: 'Tom Brown', coin: 15 },
      { name: 'Sara Green', coin: 15 },
      { name: 'Chris Black', coin: 15 },
      { name: 'Diana Silver', coin: 15 },
    ];
    return(
    <div>
      <div className='tab-headline'>Leaderboards</div>
      
      <div className='leaderboards-list'>
                {/* <div className='ref-text'>
                    Список тех, кто с тобой в команде
                </div> */}

                <div className='leader-friend'>
                {referrals.map((referral, index) => (
                    <div className='friend-item' key={index}>
                      <span className='friend-info'>
                        {index + 1}. {referral.name}
                      </span>
                      <div className='friend-right'>
                        <img src={Drop} alt="Drop Image" />
                        <span className='friend-coin'>
                          {referral.coin}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
            </div>



    </div>
  )
  }
  

export default Friends