// src/App.js
import React, { useState, useEffect } from 'react';
import LoadingScreen from './components/LoadingScreen/LoadingScreen';
// import MainMenu from './components/MainMenu/MainMenu';
// import Animation from './components/TestAnimation/TestAnimation'

import { TonConnectUIProvider } from '@tonconnect/ui-react';
import './App.css'
import { BrowserRouter as Router, Routes, Route, useLocation} from 'react-router-dom';
import AnimatedRoutes from './components/Routes/AnimatedRoutes'

const App = () => {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 3000); // 3000 мс (3 секунды) для тестирования
  }, []);

  return (
    <Router basename="/whale_bot">
      <TonConnectUIProvider manifestUrl="http://localhost:3000/whale_bot/tonconnect-manifest.json">
        <div className="App">
          <AnimatedRoutes />
        </div>
      </TonConnectUIProvider>
    </Router>
  );
};


export default App;
