import React, { useState, useEffect }  from 'react';
import './Settings.css';
import BottomMenu from '../BottomMenu/BottomMenu'
import { TonConnectUIProvider, TonConnectButton } from '@tonconnect/ui-react';
// import BottomMenu from '../TonWallet/TonWallet'
import TonWallet from '../TonWallet/TonWallet';

const Settings = () => {

    return (
        <div className='background-settings'>   
            <div className='settings-text'>
                Настройки
            </div>


            <div className="toggle-group">
                <div className="toggle-container">
                    <span className="toggle-label">Язык</span>
                    <label className="switch">
                    <input type="checkbox" />
                    <span className="slider">
                        <span className="slider-text on">RU</span>
                        <span className="slider-text off">ENG</span>
                    </span>
                    </label>
                </div>
                
                <div className="toggle-container">
                    <span className="toggle-label">Вибрация</span>
                    <label className="switch">
                    <input type="checkbox" />
                    <span className="slider">
                        <span className="slider-text on">ON</span>
                        <span className="slider-text off">OFF</span>
                    </span>
                    </label>
                </div>
                
                {/* <div className="toggle-container">
                    <span className="toggle-label">Анимация</span>
                    <label className="switch">
                    <input type="checkbox" />
                    <span className="slider">
                        <span className="slider-text on">ON</span>
                        <span className="slider-text off">OFF</span>
                    </span>
                    </label>
                </div> */}
            </div>


                <TonConnectButton className='connect-button'>
                    Connect Wallet
                </TonConnectButton>

        <BottomMenu></BottomMenu>

        </div>

        
    );
};

export default Settings;
