import React, { useState, useEffect }  from 'react';
import './Headers.css';
import Icons from "../../assets/images/Icons.svg"


// Функция, возвращающая имя пользователя
const getUserName = (username) => {
    return username; // Пример получения имени пользователя
  };

const getNumberDays = () => {
    // return Math.floor(Math.random() * Number.MAX_SAFE_INTEGER);
    return 6000;
}



function getCorrectDayWord(days) {
    const lastDigit = days % 10;
    const lastTwoDigits = days % 100;
  
    if (lastTwoDigits >= 11 && lastTwoDigits <= 14) {
      return `${days} дней`;
    }
  
    switch (lastDigit) {
      case 1:
        return `${days} день`;
      case 2:
      case 3:
      case 4:
        return `${days} дня`;
      default:
        return `${days} дней`;
    }
  }

const Headers = () => {

    const username = getUserName("test");
    const countDays = getNumberDays();
    return (
    <div className='up-menu'>
            <div className='account-menu'>
                <img className='user-icon' src={Icons}></img>
                <div className='vertical-text'>
                    <span className="user-name">{username}</span>
                    <span className='count-days'>В семье {getCorrectDayWord(countDays)}</span>
                </div>

                <div className='button-set'>
                    SET
                </div>
            </div>
        </div>
    )
}

export default Headers